.admin-button {
background-color: #007bff;
color: #fff;
border: none;
border-radius: 4px;
padding: 8px 16px;
font-size: 16px;
cursor: pointer;
}

.admin-button:hover {
background-color: #0062cc;
}

.standard-container {
display: flex;
flex-direction: column;
align-items: center;
justify-content: space-between;
background-color: #f0f0f0;
border-radius: 8px;
padding: 24px;
margin-top: 16px;
width: 50%;
margin: 10px auto;
position: relative;
}

.topic-buttons {
display: flex;
flex-direction: column;
gap: 8px;
}

.topic-buttons button {
padding: 8px 16px;
border-radius: 4px;
border: 1px solid #ccc;
background-color: #fff;
cursor: pointer;
transition: background-color 0.3s ease;
}

.topic-buttons button:hover {
    background-color: #e0e0e0;
}

.input-textarea {
    width: 100%;
    height: 150px;
    resize: none;
    margin-bottom: 10px;
}

.save-button {
align-self: flex-start;
width: 80px;
height: 30px;
font-size: 14px;
margin-top: 16px;
}

.code-mirror-container {
    width: 170%;
    height: auto;
    font-size: 16px;
    background-color: #f5f5f5;
}

.create-new-standard {
    position: absolute;
    top: 0;
    left: 10px;
    margin-left: 10px;
    padding: 10px;
    background-color: #FFFFFF;
    border-radius: 5px;
    font-weight: bold;
    cursor: pointer;
}

.remove-standard-button {
    position: absolute;
    top: 0;
    right: 0;
    background: none;
    border: none;
    cursor: pointer;
    font-size: 1.5rem;
    padding: 8px;
}

