
.drawer .MuiDrawer-paper {
    width: 240px;
    background-color: purple;
}

.standard-set-button {
    background-color: blue;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.standard-set-button:hover {
    background-color: darkblue;
}

.app-bar.small-screen {
    width: 100%;
}

.app-bar.large-screen {
    left: 0;
    top: 0;
    width: 240px;
}

.content-area.small-screen {
    margin-left: 0;
    margin-top: 64px;
    padding: 16px;
}

.content-area.large-screen {
    margin-left: 240px;
    margin-top: 0;
    padding: 16px;
}

.admin-button {
    position: fixed;
    top: 64px;
    right: 8px;
    padding: 8px 16px;
    border-radius: 12px;
    background-color: #fff;
    border: 1px solid #ccc;
}

.user-photo {
    width: 48px;
    height: 48px;
    border-radius: 50%;
    position: fixed;
    top: 8px;
    right: 8px;
}
