/* Make Tailored Set*/
.paper-container {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
    padding: 16px;
    margin-bottom: 16px;
    border: 2px solid rgba(0, 0, 0, 0.7);
    border-radius: 4px;
    box-sizing: border-box;
}

.box-container {
    overflow-x: hidden;
    position: relative;
    width: 100%;
    height: 100%;
    transition: all 0.3s ease-in-out;
}

.boxes-container {
    overflow-x: hidden;
    position: relative;
}

.grid-container {
    margin: 0 auto;
    max-width: 1200px;
}

.details-container {
    position: fixed;
    top: 0;
    right: -50%;
    width: 50%;
    height: 100%;
    background-color: white;
    z-index: 100;
    border-left: 4px solid #333;
    transition: right 0.3s ease-in-out;
}

    .details-container.show {
        right: 0;
    }

.arrow-icon {
    position: absolute;
    left: 16px;
    top: 50%;
    transform: translateY(-50%);
    cursor: pointer;
    font-size: 36px;
    display: none;
    opacity: 0;
    transition: opacity 0.3s ease-in-out;
}

    .arrow-icon.show {
        display: block;
        opacity: 1;
    }

@media (max-width: 959px) {
    .grid-container.slide,
    .box-container.slide {
        width: 50%;
        height: 50%;
        position: fixed;
        top: 0;
        left: 0;
        overflow-y: auto;
        z-index: 10;
    }
}

.content-area {
    display: flex;
    position: relative;
}

.create-set-box-container {
    z-index: 10;
    position: absolute;
    top: 0;
    left: 0;
    width: 50%;
    height: 100%;
    background-color: #f5f5f5;
    overflow: auto;
    transform: translateX(-100%);
    transition: transform 0.5s ease;
}


.create-set-box-container.show {
    transform: translateX(0%);
}
