.create-set-box {
    display: flex;
    justify-content: center;
    align-items: flex-start;
    width: 100%;
    height: 100%;
    padding: 2rem;
    box-sizing: border-box;
}

.content-wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    max-width: 50%;
    min-height: 100%;
    padding: 1rem;
    border: 2px solid black;
    box-sizing: border-box;
}

.custom-button {
    z-index: 100;
    background-color: #007bff;
    color: #fff;
    border: none;
    padding: 10px 20px;
    margin: 10px;
    border-radius: 5px;
    font-size: 16px;
    cursor: pointer;
}

    .custom-button:hover {
        background-color: #0069d9;
    }

.save-button {
    background-color: #28a745;
}

.save-button:hover {
    background-color: #218838;
}

.title-field {
    width: 80%;
    height: 50px;
    margin: auto;
    display: block;
    border: 2px solid #ccc;
    border-radius: 5px;
    font-size: 1.5rem;
    text-align: center;
    padding: 10px;
}

