.standard-container {
    width: 50%;
    margin: 0 auto;
    background-color: #f0f0f0;
    padding: 24px;
    border-radius: 8px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.topic-buttons {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.save-button {
    width: 80px;
    height: 30px;
    font-size: 14px;
    margin-top: 16px;
    background-color: #a9a9a9;
    border: 1px solid #ccc;
    border-radius: 4px;
    cursor: pointer;
    transition: background-color 0.3s ease;
}

    .save-button:hover {
        background-color: #e0e0e0;
    }

.input-container {
    margin-top: 10px;
}

.code-mirror-container {
    width: 200%;
    height: auto;
    font-size: 16px;
    background-color: #f5f5f5;
}

.code-mirror-wrapper {
    width: 90%;
}

.header-input-wrapper {
    width: 90%;
    display: flex;
    justify-content: center;
    margin-bottom: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
    padding: 4px;
}

.header-input {
    width: 100%;
    font-size: 1.5rem;
    font-weight: bold;
    border: none;
    outline: none;
    background-color: transparent;
    text-align: center;
}

