.content-area {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-bottom: 2rem;
}

.form-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-gap: 10px;
    margin: 0 auto;
    width: 66%;
    text-align: center;
}

.button-container {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-gap: 10px;
    margin: 0 auto;
    width: 66%;
    text-align: center;
    position: fixed;
    bottom: 2rem;
}

.button {
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #f5f5f5;
    border: 1px solid #ccc;
    border-radius: 4px;
    height: 100px;
    width: 100%;
    font-size: 1.5rem;
    font-family: Consolas, "Courier New", monospace;
}
