
.question-list-container {
    background-color: transparent;
    border-radius: 10px;
    overflow: hidden;
    padding: 8px;
    margin-left: 120px;
}

.question-list {
    padding: 0;
    margin-left: 120px;
}

    .question-list .MuiListItem-root {
        background-color: #4285F4; /* Chrome blue color */
        border-radius: 40px;
        margin-bottom: 16px;
        padding: 16px 24px;
    }

        .question-list .MuiListItem-root:hover {
            background-color: rgba(255, 255, 255, 0.1);
        }

    .question-list .MuiListItemText-primary {
        color: #fff;
        font-weight: 900;
        text-align: center;
        font-size: 24px;
    }
