.question-set {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.question-set-title {
    font-size: 2rem;
    margin-bottom: 0;
}

.question-set-subtitle {
    font-size: 1.5rem;
    margin-top: 0;
}

.button {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    min-width: calc(25% - 10px); /* Set a minimum width for the buttons */
    height: 50px;
    margin: 5px;
    padding: 0;
    font-size: calc(10px + 2vmin);
    text-align: center;
    border: 1px solid #ccc;
    border-radius: 4px;
    cursor: pointer;
}

.button-row {
    display: flex;
    justify-content: space-between; /* Distribute the buttons evenly */
    flex-wrap: nowrap;
    margin-bottom: 20px;
}



.button-1 {
    background-color: #f44336;
}

.button-2 {
    background-color: #4caf50;
}

.button-3 {
    background-color: #2196f3;
}

.button-4 {
    background-color: #ff9800;
}
